import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Calendar, MessageSquare, ThumbsUp, Users } from "lucide-react"
import { useQuery, gql } from '@apollo/client'

// GraphQL query to get the activity feed
const GET_USER_FEED = gql`
  query GetUserFeed($userId: ID!, $limit: Int, $offset: Int) {
    activityFeedByUser(userId: $userId, limit: $limit, offset: $offset) {
      user_id
      activities {
        activity_type
        related_entity_id
        related_entity_type
        metadata {
          firstName
          lastName
          content
          timestamp
          avatar
        }
      }
    }
  }
`

// Define the types for Activity and Metadata
interface Metadata {
  firstName: string
  lastName: string
  content: string
  timestamp: string
  avatar?: string
}

interface Activity {
  activity_type: 'event' | 'connection' | 'like' | 'comment' // Union type for activity_type
  related_entity_id: string
  related_entity_type: string
  metadata: Metadata
}

export function RecentActivity() {
  // Hardcoded user ID
  const hardcodedUserId = "a30b47d9-3e87-46cc-95af-566c91216a28"

  const { loading, error, data } = useQuery(GET_USER_FEED, {
    variables: { userId: hardcodedUserId, limit: 5, offset: 0 },
  })

  if (loading) return <p>Loading...</p>
  if (error) return <p>Error: {error.message}</p>

  const activities: Activity[] = data.activityFeedByUser.activities

  const iconMap: Record<Activity['activity_type'], JSX.Element> = {
    event: <Calendar className="h-4 w-4" />,
    connection: <Users className="h-4 w-4" />,
    like: <ThumbsUp className="h-4 w-4" />,
    comment: <MessageSquare className="h-4 w-4" />,
  }

  return (
    <div className="space-y-4">
      {activities.map((activity) => {
        const { metadata } = activity
        const timestamp = new Date(metadata.timestamp).toLocaleString()

        return (
          <div key={activity.related_entity_id} className="flex items-start space-x-4">
            <Avatar className="h-8 w-8">
              <AvatarImage src={metadata.avatar || "/placeholder.svg"} alt={`${metadata.firstName} ${metadata.lastName}`} />
              <AvatarFallback>{metadata.firstName[0]}{metadata.lastName[0]}</AvatarFallback>
            </Avatar>
            <div className="space-y-1">
              <p className="text-sm">
                <span className="font-medium">{metadata.firstName} {metadata.lastName}</span> {activity.activity_type}{" "}
                <span className="font-medium">{metadata.content}</span>
              </p>
              <p className="text-xs text-muted-foreground flex items-center">
                {iconMap[activity.activity_type]} {/* Now TypeScript knows this is safe */}
                <span className="ml-1">{timestamp}</span>
              </p>
            </div>
          </div>
        )
      })}
    </div>
  )
}
